.products {
  &-wrapper {
    display: flex;
    justify-content: space-between;
    padding-bottom: 87px;
  }

  &-block {
    max-width: 1050px;
  }

  &-sorting {
    width: 100%;
    padding: 15px 30px;
    border: 1px solid $block-bg;

    &__link {
      margin-right: 32px;
      color: $form-txt;
      font-size: 16px;
      line-height: 24px;
    }

    &__link:active {
      color: $dark;
    }
  }

  &-list {
    width: 100%;

    &__block {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 100%;
    }
  }
}

.aside {
  margin-right: 20px;

  &-menu {
    padding: 27px 31px;
    margin-bottom: 20px;
    border: 1px solid $block-bg;

    &__title {
      margin: 0;
      margin-bottom: 20px;
      font-weight: 600;
      font-size: 18px;
      line-height: 106%;
      text-transform: uppercase;
    }
  }

  &-list {
    margin: 0;
    padding: 0;
  }

  &-item {
    margin-bottom: 16px;
    list-style-type: none;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
  }

  &-link {
    margin-bottom: 8px;
    text-decoration: none;
    color: $form-txt;
  }

  &-main {
    display: none;
  }

  &-about {
    display: none;
  }

  &-basket {
    display: none;
  }

  &-contacts {
    display: none;
  }

  &-delivery {
    display: none;
  }

  &-favourites {
    display: none;
  }

  &-product {
    display: none;
  }
}

.filter {
  padding: 20px 31px;
  border: 1px solid $block-bg;

  &-title {
    margin: 0;
    padding-bottom: 17px;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
    border-bottom: 1px solid $block-bg;
  }

  &-item {
    margin: 0;
    margin-bottom: 8px;
    color: $words-color;
    font-size: 16px;
    line-height: 24px;
  }

  &-label {
    border-bottom: 1px solid $block-bg;
  }

  &-checkbox {
    height: 20px;
    width: 20px;
    background-color: $link-btn;
    // appearance: none;
    border-radius: 3px;
    cursor: pointer;
    margin: 0 11px 0 0;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
  }

  &-price {
    padding: 20px 31px 28px;
    border-bottom: 1px solid $block-bg;
    color: $dark;

    &__title {
      margin: 0;
      margin-bottom: 16px;
    }

    &__block {
      display: flex;
      justify-content: space-between;
      margin-bottom: 32px;
    }

    &--min {
      padding: 11px 30px;
      width: 125px;
      margin-right: 15px;
      color: $form-txt;
      text-align: center;
      border: 1px solid $block-bg;
      border-radius: 3px;
    }

    &--max {
      padding: 11px 30px;
      width: 125px;
      color: $form-txt;
      text-align: center;
      border: 1px solid $block-bg;
      border-radius: 3px;
    }

    &__slider {
      color: $link-btn;
    }

    &__slider {
      appearance: none;
      height: 4px;
      width: 100%;
      background-color: $block-bg;
      cursor: pointer;
    }

    &__slider::-ms-fill-lower {
      color: $link-btn;
    }
  }

  &-brand {
    padding: 20px 31px 28px;
    border-bottom: 1px solid $block-bg;
    color: $dark;

    &__block {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }

    &__title {
      margin: 0;
      margin-bottom: 16px;
    }
  }

  &-size {
    padding: 20px 31px 28px;
    border-bottom: 1px solid $block-bg;

    &__title {
      margin: 0;
      margin-bottom: 16px;
    }
  }

  &-material {
    padding: 20px 31px 28px;
    border-bottom: 1px solid $block-bg;

    &__title {
      margin: 0;
      margin-bottom: 16px;
    }
  }

  &-color {
    padding: 20px 31px 28px;
    border-bottom: 1px solid $block-bg;

    &__title {
      margin: 0;
      margin-bottom: 16px;
    }
  }

  &-functions {
    padding: 20px 31px 28px;
    // border-bottom: 1px solid $block-bg;
    &__title {
      margin: 0;
      margin-bottom: 16px;
    }
  }
}

.acor-body {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
}

.acor-container .acor-body {
  width: calc(100% - 40px);
  margin: 0 auto;
  height: 0;
  color: rgba(0, 0, 0, 0);
  background-color: $light;
  line-height: 18px;
  padding: 0 30px;
  transition: color 0.5s, padding 0.5s;
  overflow: hidden;
}

.acor-container .acor-body p {
  margin: 0 0 10px;
}

.acor-container label {
  cursor: pointer;
  background-color: $light;
  display: block;
  padding: 15px 20px;
  width: 100%;
  font-weight: 300;
  box-sizing: border-box;
  z-index: 100;
  font-size: 18px;
  margin: 0 0 5px;
  transition: color .35s;
}

.acor-container label:hover {
  color: $link-btn;
}

.acor-container input {
  display: none;
}

.acor-container label:before {
  content: '\276F';
  float: right;
  margin-left: 15px;
}

.acor-container input:checked + label {
  background-color: $link-btn;
  color: #FFF;
}

.acor-container input:checked + label:before {
  transition: transform .35s;
  transform: rotate(90deg);
}

.acor-container input:checked + label + .acor-body {
  height: auto;
  margin-top: -5px;
  color: $dark;
  padding: 20px 10px 10px;
}

