.follow {
  padding: 94px 0;
  text-align: center;

  &-title {
    margin: 0;
    margin-bottom: 14px;
    font-weight: bold;
    font-size: 28px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-subtitle {
    margin: 0;
    margin-bottom: 53px;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 20px;
  }

  &-img {
    height: 100%;
    width: 100%;
  }

  &-image {
    display: flex;
    height: 260px;
    width: 260px;
  }
}
