.reviews-page .reviews, .reviews-page.reviews {
  &-wrapper {
    padding-top: 0;
    border-top: none;
  }

  &-title {
    margin: 0;
    font-weight: 600;
    font-size: 28px;
    line-height: 106%;
    padding-bottom: 28px;
  }

  &-paragraph {
    margin: 0;
    font-size: 16px;
    line-height: 1.5;

    &--point {
      margin-bottom: 30px;
    }
  }

  &-info {
    &__title {
      margin: 0;
      margin-top: 30px;
      margin-bottom: 20px;
      font-weight: 400;
      font-size: 16px;
      line-height: 106%;
      text-transform: uppercase;
    }

    &__paragraph {
      margin: 0;
      font-size: 15px;
      line-height: 1.9;
    }

    &__list {
      margin: 0;
      padding: 0;
      margin-left: 17px;
    }

    &__item {
      padding-left: 0;
      font-size: 16px;
      line-height: 2.3;
    }
  }

  & .feedback-wrapper {
    padding-top: 28px;
  }

  & .product-feedback__stars {
    max-width: 140px;
  }

  & .product-feedback.feedback-stars {
    display: flex;
    margin-bottom: 16px;
  }

  & .product-feedback__link {
    margin-right: 2px;
  }

  & .product-feedback__txt {
    margin-left: 12px;
    border-left: 1px solid #696C74;
    padding-left: 10px;
    font-size: 18px;
    line-height: 27px;
  }

  & .reviews-wrapper {
    border-top: 1px solid #EBEBEB;
    padding: 32px 0;
  }

  & .reviews-block {
    justify-content: flex-start;
    padding-right: 0;
    width: 100%;
    max-width: 300px;
  }

  & .reviews-avatar {
    margin-right: 25px;
  }

  & .pagination {
    margin-top: -40px;
  }
}

.info--reviews {
  margin-top: 30px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (max-width: 1024px) {
  .reviews-page--top {
    padding-top: 177px;
  }
}

.popup {
  position: relative;
  max-width: 803px;
  width: 100%;
  margin: 0 auto;
  background: #FFFFFF;

  &__content {
    padding: 60px 100px;
    display: flex;
    flex-direction: column;
  }

  &__header {
    margin-bottom: 40px;
    align-self: center;
  }

  &__rate {
    display: flex;
  }

  &__stars {
    margin-left: 18px;
    margin-bottom: 40px;
    display: flex;
    flex-direction: row-reverse;

    label:hover ~ label,
    input:focus ~ label,
    label:hover,
    a:hover,
    a:hover ~ a,
    a:focus,
    a:focus ~ a {
      background: url("../img/card/star-active.svg");
    }

    input[type=radio] {
      display: none;
    }

    label {
      background: url("../img/card/star.svg");
      display: inline-block;
      width: 22px;
      height: 22px;
      cursor: pointer;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 20px;
    color: #31333A;
  }

  &__input-feedback {
    margin-top: 20px;
    min-height: 100px;
    padding: 7px 12px;
    border-radius: 5px;
    border: 1px solid #A9AAB2;

    &::placeholder {
      color: #696C74;
    }
  }

  &__close {
    position: absolute;
    display: block;
    top: 10px;
    right: 10px;
    width: 28px;
    height: 28px;
    border: none;
    background: url("../img/popup/close.svg") no-repeat;
  }

  &__feedback {
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
  }

  &__buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }

  &__submit {
    padding: 21px 52px;
    font-size: 18px;
    line-height: 106%;
    color: #FFFFFF;
    background-color: #774CA0;
    border: 1px solid #774CA0;
    border-radius: 5px;
  }

  &__reject {
    align-self: center;
    color: #D00470;
    background: url("../img/popup/arrow.svg") no-repeat left center;
    padding-left: 12px;
    display: block;
    border: none;
  }

  &__file {
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
  }

  &__file-wrapper {
    display: flex;
    margin-top: 20px;
  }

  &__input-file::file-selector-button {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }

  &__input-file {
    margin-left: 24px;
    font-style: italic;
    color: #696C74;
  }

  &__file-label {
    position: relative;
    color: #696C74;
    border-bottom: 1px dashed #696C74;
    margin-left: 15px;
    min-width: 110px;

    &::before {
      position: absolute;
      content: "";
      background: url("../img/popup/file.svg") no-repeat;
      width: 8px;
      height: 16px;
      left: -15px;
      background-size: contain;
    }
  }

  &__inputs {
    display: flex;
    flex-direction: column;
  }

  &__email {
    margin-top: 20px;
    width: 100%;
    display: flex;

    & input {
      width: 100%;
    }
  }

  &__names {
    margin-top: 13px;
    display: flex;
    justify-content: space-between;

    & input {
      width: calc(50% - 5px);
    }
  }

  &__text-input {
    height: 49px;
    padding: 7px 12px;
    border: 1px solid #A9AAB2;
    border-radius: 5px;

    &::placeholder {
      color: #696C74;
      font-size: 14px;
      line-height: 16px;
    }
  }

  &__mobile-title {
    display: none;
  }
}

.popup-wrapper {
  display: none;
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(49, 51, 58, 0.69);

  &--opened {
    display: block;
  }
}

.fixed-body {
  overflow: hidden;
}

@media (max-width: 768px) {

  .popup {
    &__header {
      display: none;
    }

    &__content {
      padding: 46px 15px 20px;
    }

    &__close {
      right: 10px;
    }

    &__names {
      display: flex;
      flex-wrap: wrap;
      column-gap: 20px;
    }

    &__buttons {
      flex-direction: column;
      margin-top: 20px;
    }

    &__reject {
      margin-top: 20px;
      order: 2;
    }

    &__names input {
      width: 100%;
      margin-bottom: 14px;
    }

    &__mobile-title {
      font-size: 18px;
      line-height: 106%;
      text-transform: uppercase;
      font-weight: 600;
      color: #31333A;
      display: block;
      margin-bottom: 30px;
    }
  }
}
