.chosen {
  &-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
  }

  &-block {
    height: 100%;
    width: 100%;

    &--wrapper {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 60px;
    }

    &-slide {
      height: 528px;
      width: 350px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-size: 18px;
    }
  }

  &-title {
    margin: 36px 0 40px;
    font-size: 28px;
    line-height: 106%;
    font-weight: bold;
    text-transform: uppercase;
  }
}

@media (max-width: 1024px) {
  .chosen {
    padding-top: 177px;
  }
}

@media (max-width: 768px) {
  .chosen-block-slide {
    height: 301px;
    width: 50%;
    align-items: flex-start;
  }

  .chosen-title {
    font-size: 18px;
    line-height: 106%;
    margin-top: 0;
    margin-bottom: 12px;
    align-self: flex-start;
  }

  .chosen-wrapper {
    flex-direction: column;
    flex-wrap: nowrap;
  }

  .chosen-block {
    display: flex;
  }
}

