.basket {
  &-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 36px 0 74px;
    border-bottom: 1px solid #A9AAB3;
  }

  &-title {
    margin: 0;
    margin-top: 36px;
    font-weight: 700;
    font-size: 28px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-container {
    width: 68%;
  }

  &-product {
    border: 1px solid $block-bg;
    border-bottom: none;
    width: 100%;

    &__top {
      display: flex;
      justify-content: space-between;
      padding: 15px 31px;
      border-bottom: 1px solid $block-bg;
    }

    &__title {
      margin: 0;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
    }

    &__container {
      width: 27%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-right: 75px;
    }
  }

  &-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 28px 31px;
    border-bottom: 1px solid $block-bg;

    &--hidden {
      display: none;
    }

    &__description {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
    }

    &__info {
      display: flex;
      align-items: center;
      width: 400px;

      &-price {
        margin: 0;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: $btn-bg;
      }

      &-ruble {
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: $btn-bg;
      }
    }

    &__title {
      margin: 0;
      margin-bottom: 6px;
      font-weight: normal;
      font-size: 16px;
      line-height: 20px;
    }

    &__price {
      display: flex;
      align-items: center;
    }
  }

  &-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 40%;
  }

  &-img {
    width: 76px;
    height: 81px;
    margin-right: 16px;
    object-fit: cover;
  }

  &-image {
    width: 76px;
    height: 81px;
  }

  &-sum {
    margin: 0;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
  }

  &-cross {
    width: 14px;
    height: 14px;
    padding: 0;
    background: $light;
    border: none;
    cursor: pointer;

    &__img {
      width: 100%;
      height: 100%;
    }
  }

  &-pay {
    width: 420px;
    height: 100%;
    margin-left: 70px;
    border: 1px solid $block-bg;

    &__top {
      display: flex;
      justify-content: space-between;
      padding: 20px 34px;
      border-bottom: 1px solid $block-bg;
    }

    &__title {
      margin: 0;
      font-weight: 600;
      font-size: 18px;
      line-height: 106%;
    }

    &__price {
      margin: 0;
      font-weight: 500;
      font-size: 18px;
      line-height: 106%;
      color: $btn-bg;
    }

    &__block {
      padding: 20px 34px;
      border-bottom: 1px solid $block-bg;
    }

    &__subtitle {
      margin: 0;
      margin-bottom: 5px;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
    }

    &__comment {
      padding: 10px;
      width: 100%;
      min-height: 106px;
      resize: none;
      outline: none;
    }
  }

  &-link {
    color: $link-btn;
    text-decoration: none;
    border-bottom: 1px solid $link-btn;
    padding-bottom: 1px;
  }

  &-button {
    padding: 21px 0;
    width: 100%;
    color: $light;
    background-color: $btn-bg;
    border: none;
    cursor: pointer;
  }

  &-block {
    padding: 20px 34px;
  }

  &-info & __btn {
    padding: 13px 28px;
    border: none;
    background-color: $btn-bg;
    color: $light;
    font-size: 18px;
    line-height: 106%;
    cursor: pointer;
  }

  //&-add {
  //  width: 45%;
  //  display: flex;
  //  justify-content: space-between;
  //  align-items: center;
  //}
  //
  //&-favourites  {
  //  padding-top: 78px;
  //}
}
