.description {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 168px 0;
  background-image: url(../img/content/bg-2.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;

  &-wrapper {
    padding: 64px 82px;
    text-align: center;
    background: rgba(255, 255, 255, 0.85);
    backdrop-filter: blur(22px);
  }

  &-block {
    max-width: 500px;
  }

  &-title {
    margin-bottom: 46px;
    font-weight: bold;
    font-size: 28px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-paragraph {
    margin: 0;
    font-weight: normal;
    font-size: 16px;
    line-height: 29px;
  }

  &-line {
    width: 118px;
    height: 3px;
    background: linear-gradient(90deg, $link-btn 0%, $btn-bg 100%);
    margin: 0 auto;
    margin-top: 46px;
  }
}
