.info {
  &-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 64px 40px 60px;
    border-top: 1px solid $form-txt;
  }

  &-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 22px;
  }

  &-item {
    &__block {
      margin-left: 20px;
    }
  }

  &-title {
    margin: 0;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-subtitle {
    margin: 0;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: $words-color;
  }
}

@media (max-width: 1200px) {
  .info-wrapper {
    border-top: none;
    padding: 0;
  }

  .info-item__block {
    margin-left: 0;
    width: 100%;
    margin-top: 26px;
  }

  .info-item {
    width: 100%;
  }

  .info-container {
    padding: 0;
    margin-bottom: 40px;
  }

  .info-item:last-child .info-container {
    margin-bottom: 0;
  }
}
