.color-filter-wrapper {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-row-gap: 2px;
  grid-column-gap: 2px;
  list-style: none;
  margin: 0;
  padding: 0;


  & .color-filter {
    width: 28px;
    height: 28px;
    display: flex;
    justify-content: center;

    &__checkbox {
      position: absolute;
      z-index: -1;
      opacity: 0;
    }

    &__label {
      width: 20px;
      height: 20px;
      display: block;
      background: red;
      border-radius: 3px;
      position: relative;
      padding: 0;
      margin: 0;
      align-self: center;

      &::before {
        content: none;
        width: 28px;
        height: 28px;
        border-radius: 3px;
        position: absolute;
        left: -4px;
        top: -4px;
        outline: 1px solid #D00470;
        background: transparent;
      }
    }
  }

  & .color-filter__checkbox:checked + .color-filter__label {
    background: red;
  }

  & .color-filter__checkbox:checked + .color-filter__label::before {
    content: "";
    transform: none;
    float: none;
    margin: 0;
  }
}

.color-filter-selected {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 26px;

  &__item {
    display: flex;
    min-height: 28px;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__color {
    width: 20px;
    height: 20px;
    display: block;
    background: red;
    border-radius: 3px;
    position: relative;
    align-self: center;

    &::before {
      content: "";
      width: 28px;
      height: 28px;
      border-radius: 3px;
      position: absolute;
      left: -4px;
      top: -4px;
      outline: 1px solid #D00470;
      background: transparent;
    }
  }

  &__text {
    color: #696C74;
    font-size: 16px;
    line-height: 24px;
    align-self: center;
    margin-left: 15px;
  }
}

