.quality {
  padding: 98px 0;
  background-image: url(../img/content/bg-3.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;

  &-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 63px 0;
    background: rgba(255, 255, 255, 0.85);
    backdrop-filter: blur(22px);
  }

  &-title {
    margin: 0;
    margin-bottom: 14px;
    font-weight: bold;
    font-size: 28px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-description {
    margin: 0;
    margin-bottom: 107px;
    text-align: center;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &-item {
    margin-left: 35px;
    margin-right: 35px;
    max-width: 280px;
    text-align: center;

    &__img {
      height: 54px;
      margin-bottom: 30px;
    }

    &__title {
      margin: 0;
      margin-bottom: 30px;
      font-weight: 600;
      font-size: 18px;
      line-height: 106%;
      text-transform: uppercase;
    }

    &__description {
      margin: 0;
      color: $words-color;
      font-size: 16px;
      line-height: 24px;
    }
  }
}



