.breadcrumbs {
  &-wrapper {
    padding: 35px 0;
  }

  &-list {
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0;
  }

  &-item {
    margin-right: 10px;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    list-style-type: none;
  }

  &-link {
    text-decoration: none;
    color: $words-color
  }

  &-img {
    margin-right: 10px;
  }
}
