.nav {
  border-bottom: 1px solid $block-bg;
}

.menu {
  position: relative;
  border-bottom: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0;
  background: $light;

  &-item {
    margin: 0;
    height: 100%;
    background: $light;
    list-style: none;

    &__second {
      margin: 0;
      margin-bottom: 10px;
      width: 100%;
      background: $light;
    }

    &__second:hover .menu-third {
      display: block;
    }

    &__third {
      margin: 0;
      margin-bottom: 10px;
      background: $light;
      list-style: none;
    }
  }

  &-item:hover {
    display: block;
    transition: .3s linear;
  }

  &-sub {
    display: none;
    position: absolute;
    left: 0;
    top: 68px;
    width: 260px;
    padding: 20px 0 12px 22px;
    margin: 0;
    background: $light;
    border: 1px solid $block-bg;
    list-style: none;
    z-index: 99;
    transition: .9s linear;
  }

  &-third {
    display: none;
    position: absolute;
    left: 259px;
    top: 68px;
    height: auto;
    width: 260px;
    padding: 20px 22px 12px;
    margin: 0;
    background: $light;
    border: 1px solid $block-bg;
    list-style: none;
    z-index: 99;
    transition: .9s linear;
  }

  &-link:hover {
    color: $link-btn;
    transition: .3s linear;
  }

  &-link {
    display: flex;
    align-items: center;
    height: 62px;
    color: $words-color;

    &__second {
      display: block;
      color: $words-color;
      text-decoration: none;
    }

    &__second:hover {
      color: $link-btn;
      transition: .3s linear;
    }

    &__third {
      display: block;
      color: $words-color;
      text-decoration: none;
    }

    &__third:hover {
      color: $link-btn;
      transition: .3s linear;
    }
  }
}





