.delivery {
  &-wrapper {
    padding-bottom: 57px;
    max-width: 1150px;
  }

  &-block {
    margin-bottom: 32px;
  }

  &-title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-paragraph {
    margin: 0;
    font-size: 16px;
    line-height: 1.5;

    &--point {
      margin-bottom: 30px;
    }
  }

  &-info {
    &__title {
      margin: 30px 0 20px;
      font-weight: 400;
      font-size: 16px;
      line-height: 106%;
      text-transform: uppercase;
    }

    &__paragraph {
      margin: 0;
      font-size: 15px;
      line-height: 1.9;
    }

    &__list {
      margin: 0;
      padding: 0;
      margin-left: 17px;
    }

    &__item {
      padding-left: 0;
      font-size: 16px;
      line-height: 2.3;
    }
  }
}
