.card {
  position: relative;
  width: 350px;
  min-height: 528px;
  display: flex;
  flex-direction: column;
  padding: 22px 28px;
  border: 1px solid $block-bg;
  background-color: $light;

  &-discount {
    position: absolute;
    top: 25px;
    left: 30px;
    padding: 5px 10px;
    background: $btn-bg;
    color: $light;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;

    &--new {
      position: absolute;
      top: 25px;
      left: 30px;
      padding: 5px 10px;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
      background-color: $link-btn;
      color: $light;
    }
  }

  &-bottom {
    margin-top: 38px;
  }

  &-favourites {
    position: absolute;
    top: 25px;
    right: 30px;
    height: 26px;
    width: 24px;
  }

  &-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 28px;

    &__img {
      height: 210px;
      width: 220px;
    }

    &__title {
      margin: 0;
      margin-top: 60px;
      margin-bottom: 18px;
      text-align: left !important;
      font-weight: 600;
      font-size: 18px;
      line-height: 106%;
      min-height: 57px;
      text-transform: uppercase;
    }
  }

  &-feedback {
    display: flex;
    align-items: center;
    margin-bottom: 15px;

    &__stars {
      max-width: 155px;
      margin: 0;
      border-right: 1px solid $words-color;
      padding-right: 8px;
    }

    &__link {
      margin-right: 2px;
    }

    &__img {
      height: 23px;
      width: 23px;
    }

    &__txt {
      margin-left: 14px;
      font-weight: normal;
      font-size: 18px;
      line-height: 150%;
      color: $words-color
    }
  }

  &-price {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 230px;

    &__main {
      color: $btn-bg;
      font-weight: normal;
      font-size: 28px;
      line-height: 106%;
      text-transform: uppercase;
    }

    &__last {
      font-size: 20px;
      color: $words-color;
      line-height: 106%;
      text-transform: uppercase;
    }

    &__del {
      position: relative;
      color: $words-color;
      text-decoration: none;
    }

    &__del:before {
      content: "";
      border-bottom: 1px solid $words-color;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 50%;
    }

    &__discount {
      padding: 3px 10px;
      background: $btn-bg;
      color: $light;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
    }
  }
}

@media (max-width: 768px) {
  .card {
    width: 100%;
    max-width: 100%;
  }
}

