.slider {
  padding: 99px 0;

  &-wrapper {
    // position: relative;
  }

  &-block {
    height: 528px;
  }

  &-title {
    margin: 0;
    margin-bottom: 58px;
    font-size: 28px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 44px;
    width: 27px;
    border: none;
    cursor: pointer;
    z-index: 99;
    background-repeat: no-repeat;
    background-position: center;
    background-color: transparent;
    // &--next
    //   right: 34px
    //   background-image: url(../img/slider/arrow-next-like.svg)
    // &--prev
    //   left: 0
    //   background-image: url(../img/slider/arrow-prev.svg)
    // &-pagination
    //   position: absolute
    //   top: 0
    //   right: 150px
    //   width: 194px
    //   height: 2px
    //   color: $words-color
  }

  &-product {
    padding-top: 0 !important;
    position: relative;

    &__wrapper {
      padding-top: 0;
      padding-bottom: 84px;
    }

    &__title {
      margin: 100px 0 43px;
      font-weight: bold;
      font-size: 28px;
      line-height: 106%;
      text-transform: uppercase;
      text-align: center;
    }
  }

  &-basket {
    &__title {
      margin: 0;
      margin-bottom: 43px;
      padding-top: 78px;
      font-weight: bold;
      font-size: 28px;
      line-height: 106%;
      text-transform: capitalize;
      text-align: center;
    }
  }

  &-favourites {
    &__title {
      padding-top: 78px;
    }
  }
}

.swiper {
  height: 100%;
  width: 100%;

  &-wrapper {
    &__product {
      display: flex;
    }
  }

  &-slide {
    height: 528px;
    width: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 18px;
    // cursor: grab;
  }

  &-pagination {
    position: absolute;
    margin: 0;
    top: 5px;
    right: 10px !important;
    width: 194px !important;
    height: 2px !important;
    // background-color: $block-bg;
    &-progressbar {
      &-fill {
        color: black;
        background-color: red;
      }
    }
  }

  &-container {
  }
}
