.footer {
  &-wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 79px 0 50px;
    border-top: 1px solid $form-txt;
    border-bottom: 1px solid #A9AAB3;
  }

  &-block {
    display: flex;
    margin-right: 95px;
    flex-direction: column;
  }

  &-title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-link {
    margin: 0;
    margin-bottom: 15px;
    color: $words-color;
    text-decoration: none;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;

    //&--active {
    //  color: transparent;
    //  background: linear-gradient(90deg, #D00470 0%, #774CA0 100%);
    //}
  }

  &-paragraph {
    max-width: 250px;
    margin: 0;
    margin-bottom: 33px;
    color: $words-color;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
  }

  &-form {
    margin-top: -5px;
    height: 40px;

    &__mail {
      width: 280px;
      padding: 10px 18px;
      margin-right: 13px;
      border: 1px solid $form-txt;
    }

    &__btn {
      height: 100%;
      padding: 6px 16px;
      border: none;
      color: $light;
      background: linear-gradient(90deg, #D00470 0%, #774CA0 100%);
      cursor: pointer;
    }

  }

  &-list {
    margin-top: -5px;
    width: 195px;
    display: flex;
    justify-content: space-between;
  }

  &-bottom {
    padding: 33px 0 48px;
    text-align: center;

    &__paragraph {
      margin: 0;
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;
    }
  }
}
