.about {
  &-wrapper {
    padding-bottom: 116px;
  }

  &-block {
    margin-bottom: 20px;
    max-width: 1150px;
  }

  &-title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-paragraph {
    margin: 0;
    font-size: 16px;
    line-height: 2.3;
  }

  &-list {
    margin: 0;
    margin-left: 17px;
    padding: 0;
  }

  &-item {
    font-size: 16px;
    line-height: 2.3;
  }
}
