.result {
  &-wrapper {
    display: flex;
  }

  &-container {
    display: flex;
    flex-direction: column;
    padding: 57px 70px 79px 0;
    border-right: 1px solid $block-bg;
    width: 630px;
  }

  &-logo {
    display: flex;
    justify-content: center;
    margin-bottom: 65px;

    &__img {
      width: 262px;
      height: 151px;
    }
  }

  &-contacts {
    margin-bottom: 37px;
  }

  &-country {
    display: flex;
    alig-items: center;
    justify-content: space-between;
  }

  &-title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
  }

  &-field {
    padding: 7px 12px 25px;
    margin-bottom: 13px;
    height: 49px;
    border-radius: 5px;
    border: 1px solid $form-txt;
    outline: none;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: $words-color;

    &--point {
      width: 100%;
    }

    &--name {
      width: 50%;
      margin-right: 10px;
    }

    &--surname {
      width: 50%;
    }

    &--post {
      width: 33%;
    }
  }

  &-name {
    display: flex;
    justify-content: space-between;
  }

  &-select {
    padding: 7px 12px 17px;
    margin-bottom: 13px;
    height: 49px;
    width: 33%;
    margin-right: 15px;
    border-radius: 5px;
    border: 1px solid $form-txt;
    outline: none;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: $words-color;
  }

  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 36px;
    border-bottom: 1px solid $block-bg;

    &__link {
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
      color: $link-btn;
    }

    &__img {
      margin-right: 12px;
    }

    &__btn {
      padding: 20px 50px;
      border: none;
      border-radius: 5px;
      font-size: 18px;
      line-height: 106%;
      color: $light;
      background-color: $btn-bg;
      cursor: pointer;
    }
  }

  &-policy {
    padding-top: 17px;

    &__link {
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;
      color: $link-btn;
    }

    &__item {
      margin-right: 56px;
    }
  }

  &-products {
    display: flex;
    flex-direction: column;
    padding: 57px 0 79px 70px;
    width: 630px;
  }

  &-box {
    margin-bottom: 0;
    padding-bottom: 14px;
    border-bottom: 1px solid $block-bg;
  }

  &-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    width: 100%;

    &-image {
      width: 76px !important;
      height: 81px !important;
      object-fit: cover;
      overflow: hidden;
      border: 1px solid $block-bg;
    }

    &__img {
      height: 100%;
      width: 100%;
      // width: calc(100% + 4px);
      // margin-top: -1px;
      // margin-left: -1px;
    }

    &__paragraph {
      margin: 0;
      margin-right: 50px;
      margin-left: 41px;
      font-size: 16px;
      line-height: 20px;
    }

    &__sum {
      margin: 0;
      font-size: 16px;
      line-height: 24px;
    }
  }

  &-coupon {
    display: flex;
    justify-content: space-between;
    padding: 25px 0;
    border-bottom: 1px solid $block-bg;

    &__field {
      width: 100%;
      height: 49px;
      padding: 16px 19px;
      outline: none;
      color: $words-color;
      border: 1px solid $form-txt;
      box-sizing: border-box;
      border-radius: 5px;
    }

    &__btn {
      padding: 15px 32px;
      margin-left: 15px;
      background-color: $btn-bg;
      color: $light;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      font-size: 18px;
      line-height: 106%;
    }
  }

  &-description {
    padding: 20px 0;
    border-bottom: 1px solid $block-bg;

    &--top {
      display: flex;
      justify-content: space-between;
      margin-bottom: 11px;
    }

    &--bottom {
      display: flex;
      justify-content: space-between;
    }

    &__paragraph {
      margin: 0;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
      color: $words-color;
    }
  }

  &-sum {
    display: flex;
    justify-content: space-between;
    padding-top: 24px;

    &__paragraph {
      margin: 0;
      font-weight: bold;
      font-size: 28px;
      line-height: 106%;
    }
  }
}




