.contacts {
  &-wrapper {
    padding-bottom: 59px;
  }

  &-title {
    margin: 0 0 35px;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-info {
    margin-bottom: 30px;
  }

  &-paragraph {
    margin: 0 0 5px;
    font-weight: 600;

    &--point {
      margin-bottom: 20px;
      font-weight: 600;
    }
  }

  &-link {
    color: $dark;
    font-weight: 400;
  }
}

