.product {
  &-wrapper {
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
  }

  &-title {
    margin: 0;
    margin-bottom: 33px;
    font-weight: bold;
    font-size: 28px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-block {
    display: flex;
    align-items: center;
    width: 100%;
    min-height: 780px;
    border: 1px solid $block-bg;
  }

  &-container {
    padding: 50px;
    height: 100%;
    width: 50%;
  }

  &-slider {
    height: 100%;
  }

  &-slide {
    width: 80%;

    &__block {
      position: relative;
      top: 0;
      left: 0;
      height: 100%;
      display: flex;
      flex-direction: column;

      &--img {
        width: 60px;
        height: 75px;
        padding: 11px 13px;
      }
    }
  }

  &-img {
    width: 100%;
    height: 100%;
  }

  &-info {
    width: 64%;
    border-left: 1px solid $block-bg;

    &__feedback {
      display: flex;
      align-items: center;
      padding: 27px 39px;
      border-bottom: 1px solid $block-bg;
    }

    &__description {
      padding: 33px 43px;
      border-bottom: 1px solid $block-bg;

      &--title {
        margin: 0;
        margin-bottom: 35px;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $words-color;
      }

      &--definition {
        margin: 0;
        font-weight: normal;
        font-size: 16px;
        line-height: 28px;
        color: $words-color;
      }
    }

    &__basket {
      padding: 40px 40px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__btn {
      padding: 16px 20px;
      background-color: $btn-bg;
      border: none;
      color: $light;
      font-size: 18px;
      line-height: 106%;
      cursor: pointer;
      border-radius: 3px;
    }

    &-feedback {
      display: flex;
      align-items: center;
      margin-right: 54px;

      &__link {
        margin-right: 6px;
      }

      &__txt {
        margin-left: 5px;
        padding-left: 10px;
        border-left: 1px solid $words-color;
        font-weight: normal;
        font-size: 18px;
        line-height: 150%;
        color: $words-color;
      }
    }

    &-favourites {
      display: flex;
      align-items: center;

      &-img {
        width: 26px;
        height: 24px;
      }

      &__txt {
        margin-left: 12px;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: $words-color;
      }
    }

    &-price {
      display: flex;
      align-items: center;

      &__title {
        margin-right: 7px;
        font-weight: 600;
        font-size: 28px;
        line-height: 106%;
        text-transform: capitalize;
      }

      &__main {
        margin-right: 4px;
        font-weight: normal;
        font-size: 28px;
        line-height: 106%;
        color: $btn-bg;
      }

      &__last {
        font-weight: normal;
        font-size: 20px;
        line-height: 106%;
        color: $words-color;
      }

      &__del {
        position: relative;
        color: $words-color;
        text-decoration: none;
      }

      &__del:before {
        content: "";
        border-bottom: 1px solid $words-color;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 50%;
      }
    }

    &-counter {
      display: flex;
      width: 155px;
      height: 50px;
      border: 1px solid $block-bg;

      &__btn {
        padding: 16px;
        border: none;
        cursor: pointer;
        background-color: $light;
        color: $words-color;
        font-weight: 600;
        font-size: 18px;
        line-height: 106%;

        &--minus {
          border-right: 1px solid $block-bg;
        }

        &--plus {
          border-left: 1px solid $block-bg;
        }
      }

      &__quantity {
        padding: 16px 5px;
        width: 70px;
        font-weight: 600;
        font-size: 18px;
        line-height: 106%;
        border: none;
        outline: none;
        text-align: center;
        color: $words-color;
      }
    }
  }
}

.feedback {
  &-wrapper {
    padding: 30px 42px 0 42px;
    border: 1px solid $block-bg;
  }

  &-block {
    padding-bottom: 32px;
  }

  &-title {
    margin: 0;
    margin-bottom: 23px;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-stars {
    margin-bottom: 23px;
  }

  &-btn {
    padding: 17px 47px;
    font-size: 18px;
    line-height: 106%;
    color: $btn-bg;
    background-color: $light;
    border: 1px solid $btn-bg;
  }
}

.reviews {
  &-wrapper {
    display: flex;
    border-top: 1px solid $block-bg;
    padding: 32px 0;
  }

  &-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 500px;
    padding-right: 36px;
    border-right: 1px solid $block-bg;
  }

  &-avatar {
    height: 94px;
    width: 94px;
    border-radius: 50%;
    overflow: hidden;
    // margin-right: 35px;
    &__img {
      width: 94px;
      height: 94px;
      object-fit: cover;
    }
  }

  &-info {
    display: flex;
    flex-direction: column;
  }

  &-name {
    margin-bottom: 7px;
    font-weight: 600;
    font-size: 18px;
    line-height: 106%;
  }

  &-date {
    margin-bottom: 7px;
    font-size: 16px;
    line-height: 24px;
    color: $words-color;
  }

  &-description {
    padding: 0 30px 0 34px;

    &__title {
      margin: 0;
      margin-bottom: 12px;
      font-weight: 600;
      font-size: 18px;
      line-height: 106%;
    }

    &__subtitle {
      margin: 0;
      font-size: 16px;
      line-height: 19px;
      color: $words-color;
    }
  }
}





