.content {
  height: 788px;
  width: 100%;
  background-image: url(../img/content/slide-1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  &-block {
  }

  &-info {
    max-width: 720px;
    padding-top: 227px;
  }

  &-title {
    margin: 0;
    margin-bottom: 32px;
    font-weight: bold;
    font-size: 48px;
    line-height: 106%;
  }

  &-subtitle {
    margin: 0 0 82px;
    font-weight: 500;
    font-size: 24px;
    line-height: 33px;
  }

  &-link {
    padding: 20px 81px;
    background: $dark;
    color: $light;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
  }
}
