.pagination {
  margin-top: -20px;

  &-wrapper {
    margin: 30px 0 30px;
    padding: 12px 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 1px solid $block-bg;

    &--chosen {
      margin: 0 0 80px;
      padding: 12px 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      border: 1px solid $block-bg;
    }
  }

  & ul {
    margin: 30px 0 30px;
    padding: 12px 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  & li {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: $dark;
    display: flex;
    width: 32px;
    height: 32px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-right: 14px;

    &:last-child {
      margin-right: 0;
    }

    &.active {
      background-color: $link-btn;
      color: $light;
      border-radius: 3px;
      width: 32px;
      height: 32px;
    }
  }

  .paginationjs-prev {
    background: url(../img/pagination/btn-prev.svg) no-repeat center;

    & a {
      display: none;
    }
  }


  .paginationjs-next {
    background: url(../img/pagination/btn-next.svg) no-repeat center;

    & a {
      display: none;
    }
  }

  &-btn {
    background-color: inherit;
    border: none;
    margin-right: 30px;
    cursor: pointer;
  }

  &-btn:last-child {
    margin-right: 0;
    margin-left: 30px;
  }
}

@media (max-width: 768px) {
  .pagination__item {
    width: 27px;
    height: 27px;
    font-size: 14px;
    line-height: 17px;
    margin-right: 6px;
  }

  .pagination {
    margin-top: -40px;
  }

  .pagination-btn {
    margin-right: 14px;
  }
}
