@media (max-width: 1400px) {
  .container {
    max-width: 1200px;
  }
  .header {
    &-logo {
      &__block {
        margin: 0;
        max-width: 300px;
      }
    }

    &-form {
      &__search {
        width: 220px;
      }
    }

    &-order {
      width: 350px;
      height: 45px;
    }

    &-basket {
      padding-left: 10px;
    }

    &-favourites {
      padding: 0 10px;
    }

    &-avatar {
      padding-right: 10px;
    }
  }

  .main {
    &-block {
      align-items: center;

      &__img {
        height: 100%;
        width: 100%;
      }
    }

    &-image {
      height: 420px;
      width: 660px;
    }
  }

  .quality {
    &-list {
      justify-content: space-around;
    }

    &-item {
      margin: 0;
      margin-bottom: 40px;
    }
  }

  .follow {
    &-block {
      justify-content: space-around;
    }

    &-image {
      display: flex;
      margin-bottom: 28px;
    }
  }

  .reviews {
    &-block {
      width: 55%;
      padding-right: 26px;
    }

    &-info {
      padding-left: 10px;
    }
  }

  .products {
    &-block {
      width: 75%;
    }

    &-list {
      &__block {
        justify-content: space-evenly;
      }
    }
  }

  .aside {
    &-wrapper {
      &--mobile {
        width: 30%;
      }
    }
  }
}

@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
  .header {
    &-order {
      width: 200px;
    }

    &-favourites {
      border-right: none;
      padding: 0;

      &__txt {
        display: none;
      }
    }

    &-basket {
      border-left: 1px solid $form-txt;

      &__txt {
        display: none;
      }
    }
  }

  .aside {
    margin-right: 9px;

    &-menu {
      padding: 0;

      &__title {
        &--media {
          margin: 0;
          padding: 20px 20px;
        }
      }
    }
  }

  .filter {
    &--media {
      padding: 0;
    }

    &-title {
      &--media {
        margin: 0;
        padding: 20px 20px;
      }
    }
  }

  .products {
    &-list {
      &__block {
        flex-wrap: wrap;
      }
    }
  }

  .menu {
    &-link {
      font-size: 14px;
      height: 58px;
    }
  }

  .content {
    height: 688px;
    color: $light;
    background-position: 100% 0;

    &-block {
      display: flex;
      justify-content: center;
      text-align: center;
    }

    &-info {
      padding-top: 187px;
    }
  }

  .slider {
    padding: 59px 0
  }

  .product {
    &-block {
      justify-content: start;
      flex-wrap: wrap;
      justify-content: center;
    }

    &-container {
      height: 600px;
      padding: 10px 0;
    }

    &-info {
      border-top: 1px solid $block-bg;
      width: 100%;
    }
  }

  .main {
    &-block {
      min-height: 420px;
    }

    &-image {
      width: 550px;
      height: 360px;
    }

    &-info {
      max-width: 360px;
    }

    &-title {
      font-size: 24px;
    }
  }

  .info {
    &-wrapper {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding-bottom: 31px;
    }

    &-container {
      flex-direction: column;
      align-items: center;
      margin-bottom: 47px;
    }

    &-item {
      &__block {
        text-align: center;
      }

      &-img {
        margin-bottom: 20px;
      }
    }
  }

  .result {
    position: relative;

    &-logo {
      position: absolute;
      left: 50%;
      top: 30px;
      right: 50%;
    }

    &-wrapper {
      flex-wrap: wrap-reverse;
    }

    &-container {
      margin: 0 auto;
      margin-top: 30px;
      padding: 0 0 34px;
      border: none;
    }

    &-products {
      margin: 0 auto;
      margin-top: 230px;
      padding: 25px;
      background-color: $block-bg;
    }

    &-box, &-coupon, &-description {
      border-bottom: 1px solid $form-txt;
    }

    &-block {
      &__paragraph {
        margin: 0;
      }

      &__sum {
        color: $btn-bg;
      }
    }
  }

  .basket {
    &-wrapper {
      display: block;
    }

    &-container {
      margin-bottom: 30px;
      width: 100%;
    }

    &-pay {
      margin-left: 0;
      width: 100%;
    }
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 760px;
  }
  .links {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 9999;
  }
  .header {
    &-form {
      display: none;

      &__btn {
        padding: 0;
      }

      &__search {
        display: none;
      }
    }

    &-top {
      padding: 17px 0
    }

    &-btn {
      height: 17px;
      width: 25px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: column;
      border: none;
      background-color: transparent;
      cursor: pointer;

      &__line {
        width: 25px;
        height: 2px;
        background-color: $dark;
      }
    }

    &--mobile {
      &-fixed {
        position: fixed;
        left: 0;
        top: 46px;
        right: 0;
        z-index: 9;
      }
    }
  }
  .nav {
    display: none;

    &--visible {
      display: block;
    }
  }

  .aside {
    &-menu {
      margin-bottom: 0;
      border-bottom: none;
    }

    &-wrapper {
      display: none;

      &--visible {
        display: block;
        position: fixed;
        right: 0;
        bottom: 0;
        top: 137px;
        background-color: $light;
        overflow-x: scroll;
        box-shadow: -500px 1000px 32px 100px rgba(34, 60, 80, 0.32);
        z-index: 99999999;
      }
    }

    &-main {
      display: block;
    }

    &-about {
      display: block;
    }

    &-basket {
      display: block;
    }

    &-contacts {
      display: block;
    }

    &-delivery {
      display: block;
    }

    &-favourites {
      display: block;
    }

    &-product {
      display: block;
    }
  }

  .breadcrumbs {
    display: none;
  }

  .about {
    &-wrapper {
      margin-top: 186px;
    }
  }

  .basket {
    &-title {
      margin-top: 186px;
    }

    &-product {
      &__container {
        width: 30%;
        margin-right: 40px;
      }
    }

    &-add {
      width: 35%;
    }
  }

  .delivery {
    &-wrapper {
      margin-top: 186px;
    }
  }

  .contacts {
    &-wrapper {
      margin-top: 186px;
    }
  }

  .products {
    &-wrapper {
      padding-top: 180px;
      width: 100%;
      justify-content: start;
    }

    &-block {
      width: 100%;
    }

    &-list {
      &__block {
        justify-content: space-between;
      }
    }

    &-sorting {
      width: 100%;
    }
  }

  .content {
    height: 550px;

    &-title {
      font-size: 40px;
    }

    &-subtitle {
      font-size: 20px;
    }

    &-info {
      padding-top: 90px;
    }

    &-block {
      margin-top: 135px;
    }
  }

  .product {
    &-wrapper {
      margin-top: 180px;
    }

    &-container {
      height: 430px;
    }

    &-slide {
      height: 100%;
    }

    &-img {
      height: 100%;
    }

    &-info {
      width: 100%;
    }

    &-slider {
      width: 100%;
    }
  }

  .main {
    &-block {
      justify-content: center;

      &__img {
        width: 100%;
      }
    }

    &-info {
      margin-top: 20px;
      margin-bottom: 20px;
      text-align: center;
    }

    &-line {
      margin: 0 auto;
    }

    &-description {
      margin-top: 28px;
    }

    &-image {
      width: 100%;
      margin-right: 0;
      margin-bottom: 28px;

      &--point {
        margin-right: 0;
      }
    }
  }
  .footer {
    &-block {
      &--links {
        margin-top: 30px;
      }
    }
  }

  .reviews {
    &-wrapper {
      flex-wrap: wrap;
    }

    &-block {
      justify-content: start;
      padding: 0;
      margin-bottom: 29px;
      border: none;
    }

    &-description {
      padding: 0;
    }
  }

  .card {
    &-products {
      margin-top: 30px;
    }
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 700px;
    width: 92%;
  }
  .main {
    &-wrapper {
      padding-top: 0;
    }

    &-block {
      width: 100%;
      margin-bottom: 0;

      &__img {
        width: 100%;
      }
    }

    &-image {
      margin: 0;
      width: 110%;
    }

    &-info {
      margin: 65px 0 80px;
    }
  }
  .quality {
    &-description {
      margin-bottom: 68px;
    }
  }
  .footer {
    &-wrapper {
      justify-content: space-evenly;
      padding-bottom: 0;
    }

    &-block {
      margin-bottom: 47px;

      &--follow {
        width: 100%;
        justify-content: start;
        align-items: center;
      }

      &--links {
        width: 100%;
        align-items: center;
      }
    }

    &-paragraph {
      max-width: 280px;
    }
  }
  .card {
    padding: 22px 13px;
    height: 100%;
    min-height: 301px !important;

    &-content {
      margin-top: 5px;
      &__title {
        font-size: 14px;
        line-height: 17px;
        margin-top: 30px;
        margin-bottom: 0;
        text-transform: none;
      }

      &__img {
        height: 110px;
        width: 120px;
      }
    }

    &-feedback {
      margin-bottom: 10px;

      &__img {
        width: 10px;
        height: 10px;
      }

      &__link {
        margin-right: 0;
      }

      &__stars {
        max-width: 130px;
        padding-right: 4px;
      }

      &__txt {
        font-size: 11px;
        margin-left: 4px;
      }
    }

    &-price {
      &__main {
        font-size: 14px;
        margin-right: 8px;
      }

      &__last {
        margin-right: 8px;
        font-size: 14px;
      }

      &__discount {
        padding: 2px 6px;
        font-size: 14px;
        line-height: 17px;
      }
    }

    &-discount {
      padding: 2px 6px;
      font-size: 14px;
      left: 13px;
      top: 17px;
      line-height: 17px;

      &--new {
        padding: 2px 6px;
        font-size: 14px;
        left: 13px;
        top: 22px;
      }
    }

    &-favourites {
      right: 17px;
      top: 19px;

      &__img, img {
        width: 19px;
        height: 17px;
      }
    }

    &-bottom {
      margin-top: 4px;
    }
  }

  .product {
    &-info {
      &__basket {
        flex-direction: column;
        justify-content: start;
        align-items: start;
      }

      &__btn {
        margin-top: 20px;
      }
    }

    &-price {
      margin-bottom: 20px;
    }

    &-feedback {
      margin-bottom: 10px;

      &__img {
        height: 13px;
        width: 13px;
      }

      &__link {
        margin-right: 4px;
      }
    }

    &-counter {
      width: 155px;
      // &__quantity
      //   width: 50px
    }
  }

  .basket {
    &-block {
      justify-content: start;
      align-items: start;
      flex-direction: column;

      &__info {
        margin-bottom: 20px;
      }
    }

    &-product {
      &__top {
        display: none;
      }
    }

    &-box {
      width: 100%;
    }

    &-add {
      width: 100%;
    }
  }

  .reviews {
    &-block {
      width: 100%;
    }

    &-avatar {
      height: 64px;
      width: 64px;

      &__img {
        width: 100%;
        height: 100%;
      }
    }

    &-name {
      font-size: 16px;
    }

    &-date {
      font-size: 14px;
    }
  }

  .feedback {
    &-btn {
      width: 100%;
    }
  }

  .slider {
    &-block {
      height: 400px;
    }
  }

  .swiper {
    &-slide {
      height: 100%;
    }
  }
}

@media (max-width: 620px) {
  .header {
    background-color: $btn-bg;

    &-top {
      flex-direction: row-reverse;
      justify-content: space-between;
      padding: 22px 0;
    }

    &-order {
      width: auto;
      margin-left: 50%;
    }

    &-logo {
      &__block {
        display: none;
        position: absolute;
        top: -40px;
        left: 5%;
      }
    }

    &-basket {
      border: none;
    }

    &-favourites {
      &__img {
        display: none;

        &--mobile {
          display: block;
        }
      }

      &__counter {
        background-color: $link-btn;
      }
    }

    &-basket {
      &__img {
        display: none;

        &--mobile {
          display: block;
        }
      }

      &__counter {
        background-color: $link-btn;
      }
    }

    &-btn {
      &__line {
        background-color: $light;
      }
    }
  }

  .description {
    padding: 100px 0;

    &-wrapper {
      padding: 42px 52px;
    }

    &-title {
      margin-top: 0;
    }
  }

  .main {
    &-image {
      height: 300px;
    }
  }

  .follow {
    padding-bottom: 50px;

    &-image {
      margin-right: 5px;
    }
  }

  .product {
    &-container {
      height: 340px;
      width: 65%;
    }

    &-slide {
      width: 100%;
    }

    &-img {
      width: 100%;
    }
  }

  .slider {
    &-basket {
      &__title {
        padding: 0;
      }
    }
  }

  .links {
    &-item {
      margin-right: 12px;
    }
  }

  .products {
    &-sorting {
      display: flex;
      flex-direction: column;
      padding: 10px 15px;
    }
  }
}

@media (max-width: 480px) {
  .content {
    height: 520px;

    &-title {
      font-size: 28px;
      margin-bottom: 36px;
    }

    &-subtitle {
      font-size: 16px;
      margin-bottom: 36px;
    }
  }

  .main {
    &-image {
      height: 230px;
    }
  }

  .footer {
    &-wrapper {
      padding-top: 41px;
    }

    &-block {
      margin-bottom: 30px;

      &--follow {
        margin-bottom: 0;
      }

      &--catalog {
        margin-right: 10px;
      }

      &--information {
        margin-right: 0;
      }
    }

    &-form {
      display: flex;
      justify-content: space-between;

      &__mail {
        width: 50%;
      }

      &__btn {
        width: 40%;
        padding: 6px 10px;
      }
    }
  }

  .basket {
    &-wrapper {
      padding-bottom: 0;
      border: none;
    }

    &-product {
      border: none;
    }

    &-block {
      padding: 0;
      padding-bottom: 20px;
      margin-bottom: 35px;
      border: none;
      border-bottom: 1px solid #EBEBEB;

      &__info {
        width: 100%;
      }
    }

    &-description {
      padding: 22px 0 28px;
    }

    &-pay {
      padding: 0;
      border: none;

      &__top {
        padding: 0 0 19px;
        border: none;
      }

      &__block {
        padding: 0 0 28px;
      }
    }

    &-button {
      padding: 13px 0;
      text-align: center;

      &-block {
        padding: 0;
      }
    }
  }
  .slider {
    &-container {
      width: 100%;
    }

    &-basket {
      &__title {
        font-size: 20px;
      }
    }
  }
  .swiper {
    &-slide {
      width: 100%;
    }
  }
  .product {
    &-title {
      margin-bottom: 0;
      font-size: 16px;
      text-align: center;
    }

    &-block {
      border: none;
    }

    &-container {
      padding: 30px 0;
      width: 80%;
    }

    &-info {
      border: none;

      &__feedback {
        padding: 0;
        margin-bottom: 20px;
        border: none;
        justify-content: space-between;
      }

      &__description {
        padding: 0;
        border: none;
        margin-bottom: 50px;

        &--title {
          margin-bottom: 25px;
          font-size: 14px;

          &--definition {
            font-size: 14px;
          }
        }
      }

      &__basket {
        margin-bottom: 50px;
        padding: 0;
        border: none;
      }

      &__btn {
        width: 100%;
      }
    }

    &-feedback {
      margin: 0;
    }

    &-favourites {
      &__txt {
        display: none;
      }
    }
  }
  .feedback {
    &-wrapper {
      margin-bottom: 0;
      padding: 0;
      border: none;
    }
  }
  .reviews {
    &-wrapper {
      border-top: none;
      border-bottom: 1px solid $block-bg;
    }
  }
  .result {
    &-bottom {
      flex-direction: column-reverse;

      &__btn {
        width: 100%;
        margin-bottom: 18px;
      }

      &-policy {
        display: flex;
        flex-direction: column;

        &__item {
          margin: 0;
          margin-bottom: 11px;
        }
      }
    }
  }
  .products {
    &-list {
      &__block {
        align-items: center;
        justify-content: center;
      }

      &-sorting {
        align-items: center;
        justify-content: center;
      }
    }
  }
}

