.main {
  &-container {
    position: relative;
  }

  &-wrapper {
    padding-top: 48px;
  }

  &-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap-reverse;
    margin-bottom: 35px;
    min-height: 527px;

    &__img {
      width: 100%;
      height: 100%;
    }
  }


  &-image {
    height: 100%;
    max-width: 800px;
    z-index: -1;

    &--point {
      margin-right: 35px;
    }
  }

  &-image:nth-first-child(odd) {
    margin-left: 35px;
  }

  &-block:nth-child(even) {
    flex-direction: row-reverse;
  }

  &-info {
    max-width: 500px;
  }

  &-title {
    margin: 0;
    margin-bottom: 28px;
    font-weight: bold;
    font-size: 28px;
    line-height: 106%;
    text-transform: uppercase;
  }

  &-line {
    width: 220px;
    height: 4px;
    margin-bottom: 28px;
    background: linear-gradient(90deg, $link-btn 0%, $btn-bg 100%);
  }

  &-description {
    margin: 0;
    margin-bottom: 28px;
    font-size: 16px;
    line-height: 24px;
  }

  &-link {
    color: $dark;
    font-size: 16px;
    line-height: 19px;
    border-bottom: 1px solid $dark;

