.header {
  background-color: $light;
  border-bottom: 1px solid $block-bg;

  &-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 21px 0;

    &__menu {
      position: relative;
      width: 87px;
      height: 100%;
      padding: 12px 21px;
      margin: 0 auto;
      font-size: 16px;
      line-height: 19px;
      background: $light;
      color: $form-txt;
      outline: none;
      cursor: pointer;
      border: 1px solid #EBEBEB;
    }

    &__menu::after {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      right: 20px;
      top: 50%;
      // background-image: url(../img/menu/arrow-down-1.svg);
      // background-size: cover;
      // background-repeat: no-repeat;
      // z-index: 99;
      background-color: #696C74;
      margin-top: -6px;
      border-width: 6px 0 6px 6px;
      border-style: solid;
      border-color: transparent $light;
    }
  }

  &-btn {
    display: none;

    &__list {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      background: #fff;
      font-weight: normal;
      opacity: 0;
      pointer-events: none;
    }

    &__item {
      // display: block;
      color: black;
      padding: 10px 20px;
    }

    &__link {
      text-decoration: none;

    }

    &__link:hover {
      background: #f3f8f8;
    }
  }

  &-logo {
    width: 100%;

    &__block {
      max-width: 341px;
      margin-right: 40px;
    }
  }

  &-form {
    margin-bottom: 5px;
    margin-left: 154px;
    height: 44px;
    display: flex;
    align-items: center;
    flex-direction: row;
    font-size: 16px;
    line-height: 19px;
    color: $form-txt;

    &__search {
      height: 100%;
      width: 350px;
      font-size: 16px;
      line-height: 19px;
      color: $form-txt;
      border: 1px solid $block-bg;
      outline: none;
    }

    &__btn {
      height: 100%;
      padding: 0 23px;
      border: none;
      background-color: $btn-bg;
      cursor: pointer;
    }
  }

  &-select {
    width: 87px;
    padding: 13px 21px;
    font-size: 16px;
    line-height: 19px;
    color: $form-txt;

    &__item {
      padding: 13px 21px;
      font-size: 16px;
      line-height: 19px;
      color: $form-txt;
    }
  }

  &-order {
    display: flex;
    flex-direction: row;
    justify-content: end;
    width: 390px;
    height: 45px;
    padding-right: 5px;
  }

  // &-avatar {
  //   display: flex;
  //   flex-direction: row;
  //   align-items: center;
  //   padding-right: 22px;
  //   border-right: 1px solid $form-txt;

  //   &__btn {
  //     border: none
  //     background-color: transparent
  //     padding: 0
  //     padding-right: 7px
  //      }
  //  }

  &-favourites {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 22px;

    &__block {
      position: relative;
      display: block;
      margin-right: 13px;
    }

    &__img {
      width: 27px;
      height: 24px;

      &--mobile {
        display: none;
      }
    }

    &__counter {
      position: absolute;
      right: -3px;
      top: -3px;
      width: 18px;
      height: 18px;
      text-align: center;
      background: $btn-bg;
      color: $light;
      border-radius: 50%;
      font-weight: 400;
      font-size: 11px;
      line-height: 150%;
      padding: 1px;
    }

    &__txt {
      font-size: 16px;
      line-height: 24px;
      color: $dark;
    }
  }

  &-basket {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 22px;
    border-left: 1px solid $form-txt;

    &__block {
      position: relative;
      display: block;
      margin-right: 14px;
    }

    &__img {
      width: 30px;
      height: 28px;

      &--mobile {
        display: none;
      }
    }

    &__counter {
      position: absolute;
      right: -4px;
      top: -3px;
      width: 17px;
      height: 17px;
      text-align: center;
      background: $btn-bg;
      color: $light;
      border-radius: 50%;
      font-weight: 400;
      font-size: 11px;
      line-height: 150%;
      padding: 1px;
    }

    &__txt {
      color: $btn-bg;
      font-weight: 600;
      font-size: 18px;
      line-height: 150%;
    }
  }
}
